@font-face {
    font-family: adilla;
    src: url(../Fonts/adelia.otf);
}
.add-container{
    width: 100%;
    height: auto;
    background-color: rgb(210, 210, 210);
    display: flex;
    flex-wrap: wrap;
    justify-content: right;
}
.add-image-container{
    display: flex;
    float: right;
    width: 30%;
    background-color: white;
    margin: 1%;
    min-width: 300px;
}
.add-image-container img{
    width: 100%;
}
.add-text-container{
    min-width: 300px;
    width: 40%;
    padding: 0% 10%;
}
.add-text-container h2{
    color: white;
    font-family: adilla;
    font-size:calc(19px + 2vw);
    font-weight: 200;
}
.add-container p{
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: large;
    font-weight: 300;
    font-size: 12px;
}